<template>
  <div class="botupchat-footer-section">
    <div class="botupchat-text-area-icons">
      <div class="botupchat-footer-icons">
        <b-form @submit.prevent="submit">
          <b-form-input
            class="pr-3 bg-input-color"
            id="editor"
            v-model="content"
            type="text"
            size="lg"
            required
            oninvalid="Enter at least 5 characters"
            :state="content.length >= 10"
            onchange="setCustomValidity('')"
            placeholder="Write a reply..."
            @blur="submit()"
            v-on:keyup.enter="$event.target.blur()"
            aria-describedby="input-live-help input-url-feedback"
          />
          <button class="botupchat-send-icon" type="submit" v-if="add_button">
            <svg class="botup-send-img" :disabled="button_disable || !content || content.length > max_length" viewBox="0 0 29 29" fill="none" xmlns="http://www.w3.org/2000/svg">
              <path d="M0 0L29 14.5L0 29L8.5 14.5L0 0Z" fill="#4E71EE" />
            </svg>
          </button>
        </b-form>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    props: {
      enableChat: Boolean,
      max_length: {
        type: Number,
        default: 1000, // Text validation
      },
    },
    data() {
      return {
        UIPrefs: window._Botup.UIPrefs.prefs,
        content: "",
        button_disable: false,
        add_button: true,
      };
    },
    methods: {
      /**
       * Event fires when user send message
       * @param {Object} message  Invoke message which is given by user
       */
      submit() {
        window.refresh_loading = true;
        if (!this.content) return;
        window._Botup.State.commit("setActiveMessage", this.content);

        // Set tone while user replay
        window._Botup.UIPrefs.play(this.UIPrefs.chat.sound.playMessageOut);

        // To Remove existing text content in Quill editor
        if (document.getElementsByClassName("editor")) document.getElementById("editor").value = null;

        if (this.enableChat)
          this.$router.push({
            name: "chat",
            params: { active: true },
          });
      },
    },
  };
</script>
<style>
  .flex-column-reverse {
    width: 320px;
  }
  .botupchat-footer-section {
    border-bottom-left-radius: -4px;
    border-bottom-right-radius: -10px;
  }
  .botupchat-footer-section .botupchat-text-area-icons {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #ececec !important;
  }
  .botupchat-footer-section textarea {
    box-sizing: border-box;
    padding: 0px 50px 0px 0px;
    width: 100%;
    height: 100%;
    font-family: "Poppins", sans-serif;
    font-size: 14px;
    font-weight: normal;
    line-height: 1.33;
    background-color: rgb(247 247 248);
    white-space: pre-wrap;
    overflow-wrap: break-word;
    border: 0;
  }
  .botupchat-footer-section textarea:focus-visible {
    outline: none;
    border: 0;
  }
  button {
    background: none;
    border: unset;
  }

  .botupchat-footer-section .textarea-emoji-control {
    height: 17px;
    right: 85px;
    top: 50px;
    width: 17px;
    cursor: pointer;
  }
  .botupchat-footer-section .ql-editor.ql-blank::before {
    font-style: normal;
    left: 0px !important;
  }
  .botupchat-footer-section .ql-toolbar.ql-snow {
    border: 0px solid #ccc;
    box-sizing: border-box;
    padding: 0px;
    position: absolute;
    bottom: -23px !important;
    left: -7px !important;
  }
  .botupchat-footer-section .ql-toolbar.ql-snow .ql-formats .ql-picker {
    top: 4px;
  }
  .botupchat-footer-section .ql-editor {
    max-height: 39px;
    min-height: 39px;
    padding-top: 8px !important;
  }
  .botupchat-footer-section .ql-editor p span {
    color: #000 !important;
    font-size: 12px !important;
  }
  .botupchat-footer-section .ql-container {
    max-height: 35px;
    font-family: "Poppins", sans-serif;
  }
  .botupchat-footer-section .ql-snow.ql-toolbar button svg,
  .ql-snow .ql-toolbar button svg {
    width: 15px;
    height: 15px;
  }
  .botupchat-footer-section .ql-snow .ql-color-picker .ql-picker-label svg,
  .ql-snow .ql-icon-picker .ql-picker-label svg {
    width: 15px;
    height: 15px;
  }
  .margin-zero p {
    margin: 0;
  }
  .botupchat-footer-section .ql-toolbar.ql-snow .ql-formats {
    margin-right: 0px;
  }
  .botupchat-footer-section .ql-snow .ql-picker.ql-expanded .ql-picker-options {
    top: -103px;
  }
  .botupchat-footer-section .ql-snow .ql-color-picker .ql-picker-label,
  .ql-snow .ql-icon-picker .ql-picker-label {
    padding: 0px 4px;
  }
  .chat_icon .btn:focus,
  .btn.focus {
    outline: 0;
    box-shadow: 0 0 0 0.2rem rgb(0 123 255 / 0%);
  }
  .botupchat-footer-section .ql-snow .ql-stroke {
    stroke: #aeabab;
    stroke-width: 1.5;
  }
  .botupchat-footer-section .ql-snow .ql-fill,
  .ql-snow .ql-stroke.ql-fill {
    fill: #aeabab;
  }
  .botupchat-btn-light:hover {
    background-color: #e4e5ff !important;
  }
  .botupchat-btn-light:focus,
  .botupchat-btn-light.focus {
    box-shadow: 0 0 0 0.2rem rgb(130 138 145 / 0%) !important;
  }
  .textarea-emoji-control:hover .ql-stroke {
    stroke: #06c !important;
  }
  .textarea-emoji-control:hover .ql-fill {
    fill: #06c !important;
  }
  .bg-input-color {
    background: #f7f7f8 !important;
    width: 285px !important;
  }
  .botupchat-footer-section .botupchat-footer-icons {
    height: 30px !important;
  }
  .botup-chat .botupchat-footer-section {
    padding: 15px 20px 0px !important;
  }
  .botup-chat .botupchat-footer-section .botup-send-img {
    bottom: 1px !important;
  }
  .botup-ultra .botupchat-bg-reply-ultra,
  .botup-bubble .botupchat-bg-reply-bubble,
  .botup-social .botupchat-bg-reply-social {
    word-break: break-all !important;
  }
</style>



